import React, { useState, useEffect } from "react";
import { useTransition, animated as a } from "react-spring";
import { Image } from "semantic-ui-react";
import shuffle from "lodash/shuffle";
import useMeasure from "./useMeasure";
import useMedia from "./useMedia";
import "./AnimatedGallery.css";
import mobgenners from "../../constants/mobgenners";

export default function AnimatedGallery() {
  const columns = useMedia(
    ["(min-width: 1440px)", "(min-width: 1000px)", "(min-width: 600px)"],
    [4, 3, 3],
    3
  );
  const [bind, { width }] = useMeasure();
  const [items, set] = useState(mobgenners);
  useEffect(() => void setInterval(() => set(shuffle), 5000), []);

  const heights = new Array(columns).fill(0); // Each column gets a height starting with zero
  const gridItems = items.map((child, i) => {
    const column = heights.indexOf(Math.min(...heights)); // Basic masonry-grid placing, puts tile into the smallest column using Math.min
    const xy = [
      (width / columns) * column,
      (heights[column] += window.innerWidth / (columns + 1) + 20)
    ]; // X = container width / number of columns * column index, Y = it's just the height of the current column
    return {
      ...child,
      xy,
      width: window.innerWidth / (columns + 2),
      height: window.innerWidth / (columns + 2)
    };
  });

  // This turns gridItems into transitions, any addition, removal or change will be animated
  const transitions = useTransition(gridItems, item => item.image, {
    from: ({ xy, width, height }) => ({ xy, width, height, opacity: 0 }),
    enter: ({ xy, width, height }) => ({ xy, width, height, opacity: 1 }),
    update: ({ xy, width, height }) => ({ xy, width, height }),
    leave: { height: 0, opacity: 0 },
    config: { mass: 5, tension: 180, friction: 50 },
    trail: 30
  });

  return (
    <div {...bind} className="list" style={{ height: Math.max(...heights) }}>
      {transitions.map(({ item, props: { xy, ...rest }, key }) => (
        <a.div
          key={key}
          style={{
            transform: xy.interpolate((x, y) => `translate3d(${x}px,${y}px,0)`),
            ...rest
          }}
        >
          <Image style={{ width: "100%", height: "100%" }} src={item.image} />
        </a.div>
      ))}
    </div>
  );
}
