import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";
import colors from "../../constants/colors";
import logo from "../../assets/img/logo-ai.png";
import MenuMobile from "./MenuMobile";

const styles = {
  navBar: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between"
  },
  faBarsIconDiv: {
    width: "32px",
    height: "32px",
    padding: "7px",
    alignSelf: "center",
    alignContent: "flex-start"
  },
  logoDiv: {
    margin: "10px",
    width: "100%",
    height: "36px",
    alignSelf: "center",
    alignContent: "flex-start"
  },
  faBarsIcon: {
    cursor: "pointer",
    color: colors.white,
    width: "18px !important",
    height: "18px !important"
  },
  logo: {
    padding: "0px",
    width: "150px !important",
    height: "auto !important"
  },
  faIconDiv: {
    width: "32px",
    height: "32px",
    padding: "7px",
    alignSelf: "center",
    alignContent: "center",
    textAlign: "center"
  },
  faIcon: {
    cursor: "pointer",
    color: colors.white,
    width: "24px !important",
    height: "24px !important",
    "&:hover": {
      color: colors.orange
    }
  },
  narrowLinks: {
    // height: "100vh",
    width:"50vw",
    "& a": {
      textDecorarion: "none",
      // display: "block",
      float: "left",
      clear: "left",
      padding: "0.5em 0"
    }
  },
  showMenu: {
    display: "block"
  },
  hiddenMenu: {
    display: "none"
  },
  linkLogo: {
    color: colors.white
  },
  link: {
    "&.userIcon": {
      width: "118px"
    }
  }
};

const NavBarMobile = ({ classes, onGoToPage }) => {
  const [isOpenMenu, setIsOpenMenu] = useState(false);

  return (
    <div>
      <div className={classes.navBar}>
        <div className={classes.logoDiv}>
          <Image src={logo} className={classes.logo} />
        </div>
        <div className={classes.faBarsIconDiv}>
          <FontAwesomeIcon
            icon={isOpenMenu ? faTimes : faBars}
            onClick={() => setIsOpenMenu(!isOpenMenu)}
            className={classes.faBarsIcon}
          />
        </div>
      </div>
      <div
        className={`${classes.narrowLinks}
                    ${isOpenMenu ? classes.showMenu : classes.hiddenMenu}`}
      >
        <MenuMobile onGoToPage={onGoToPage} />
      </div>
    </div>
  );
};

NavBarMobile.propTypes = {
  classes: PropTypes.object,
  onGoToPage: PropTypes.func
};

export default withStyles(styles)(NavBarMobile);
