import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import Gallery from "./Gallery";
import texts from "../../constants/texts";
import colors from "../../constants/colors";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    height: "150%",
    width: "100%",
    display: "flex",
    position: "relative",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
    color: colors.white
  }),
  title: {
    display: "block",
    width: "100%",
    fontSize: "64px",
    marginBottom: "120px",
    marginRight: "40%",
    textAlign: "right"
  }
};

const VideoGallerySection = ({ classes, offset }) => (
  <ParallaxLayer offset={offset} speed={0.1}>
    <div className={classes.componentDiv}>
      <h1 className={classes.title}>{texts.S5_TITLE}</h1>
      <Gallery />
    </div>
  </ParallaxLayer>
);

VideoGallerySection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(VideoGallerySection);
