import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import { InView } from "react-intersection-observer";
import CountUp from "react-countup";
import AnimatedGallery from "./AnimatedGallery";
import texts from "../../constants/texts";

const styles = {
  sectionDiv: props => ({
    backgroundColor: props.color,
    width: "100%"
  }),
  componentDiv: props => ({
    backgroundColor: props.color,
    width: "80%",
    margin: "12%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center"
  }),
  textDiv: {
    width: "90%",
    marginLeft: "10%",
    fontWeight: "bold",
    fontSize: "32px",
    marginTop: "110px",
    marginBottom: "10px"
  },
  galleryDiv: {
    width: "100%"
  }
};

const MobgennersSection = ({ classes, offset }) => (
  <ParallaxLayer
    offset={offset}
    factor={2.5}
    speed={0}
    className={classes.sectionDiv}
  >
    <div className={classes.componentDiv}>
      <div className={classes.textDiv}>
        {/* PEOPLE */}
        <div className={classes.lineText}>
          <CountUp start={0} end={texts.S6_LINE1_NUM}>
            {({ countUpRef, start }) => (
              <div>
                <InView as="div" threshold={0.3} onChange={start} />
                <span ref={countUpRef} /> {texts.S6_LINE1_TXT1}
              </div>
            )}
          </CountUp>
        </div>
        {/* Nationalites */}
        <div className={classes.lineText}>
          <CountUp start={0} end={texts.S6_LINE2_NUM}>
            {({ countUpRef, start }) => (
              <div>
                <InView as="div" threshold={0.3} onChange={start} />
                <span ref={countUpRef} /> {texts.S6_LINE2_TXT1}
              </div>
            )}
          </CountUp>
        </div>
        {/* Dogs */}

        <div className={classes.lineText}>
          <CountUp start={0} end={texts.S6_LINE3_NUM}>
            {({ countUpRef, start }) => (
              <div>
                <InView as="div" threshold={0.3} onChange={start} />
                <span ref={countUpRef} /> {texts.S6_LINE3_TXT1}
              </div>
            )}
          </CountUp>
        </div>
      </div>
      <div className={classes.galleryDiv}>
        <AnimatedGallery />
      </div>
    </div>
  </ParallaxLayer>
);

MobgennersSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number
};

export default withStyles(styles)(MobgennersSection);
