import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import { ParallaxLayer } from "react-spring/renderprops-addons";
import Header from "../Header";

const styles = {
  componentDiv: props => ({
    backgroundColor: props.color,
    overflow: "hidden",
    width: "100%",
    height: "100%",
    display: "block",
    position: "relative"
  }),
  contentDiv: {
    width: "100%",
    height: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  }
};

const VideoSection = ({ classes, offset, onGoToPage }) => (
  <ParallaxLayer offset={offset} speed={0.1}>
    <div className={classes.componentDiv}>
      <Header onGoToPage={onGoToPage} />
      <div className={classes.contentDiv}>Video</div>
    </div>
  </ParallaxLayer>
);

VideoSection.propTypes = {
  classes: PropTypes.object,
  offset: PropTypes.number,
  onGoToPage: PropTypes.func
};

export default withStyles(styles)(VideoSection);
