import React from "react";
import PropTypes from "prop-types";
import withStyles from "react-jss";
import colors from "../../constants/colors";

const styles = {
  tag: {
    display: "none",
    backgroundColor: colors.black,
    color: colors.white,
    position: "relative",
    width: "138px",
    height: "12px",
    lineHeight: "12px",
    padding: "10px 0px",
    right: "122px",
    top: "-40px",
    zIndex: "999",
    cursor: "pointer",
    "&:hover": {
      display: "block"
    }
  },
  dot: {
    display: "block",
    width: "16px",
    height: "16px",
    marginBottom: "16px",
    position: "relative",
    "&:hover + .tags": {
      display: "block",
      height: "16px"
    },
    "& .link": {
      display: "block",
      position: "relative",
      zIndex: "1",
      width: "100%",
      height: "100%",
      cursor: "pointer",
      textDecoration: "none",
      marginLeft: "0px",
      border: "0px",
      "&:focus": {
        outline: "none"
      }
    },
    "&.dot1 .link": {
      backgroundColor: colors.dot1
    },
    "&.dot2 .link": {
      backgroundColor: colors.dot2
    },
    "&.dot3 .link": {
      backgroundColor: colors.dot3
    },
    "&.dot4 .link": {
      backgroundColor: colors.dot4
    },
    "&.dot5 .link": {
      backgroundColor: colors.dot5
    },
    "&.dot6 .link": {
      backgroundColor: colors.dot6
    },
    "&.dot1.active .link": {
      backgroundColor: colors.dot1Active
    },
    "&.dot2.active .link": {
      backgroundColor: colors.dot2Active
    },
    "&.dot3.active .link": {
      backgroundColor: colors.dot3Active
    },
    "&.dot4.active .link": {
      backgroundColor: colors.dot4Active
    },
    "&.dot5.active .link": {
      backgroundColor: colors.dot5Active
    },
    "&.dot6.active .link": {
      backgroundColor: colors.dot6Active
    },
    "&:hover .link": {
      backgroundColor: colors.black
    }
  },
  linkTag: {
    backgroundColor: colors.black,
    color: colors.white,
    display: "block",
    width: "100%",
    height: "100%",
    border: "0px",
    cursor: "pointer",
    fontSize: "16px",
    lineHeight: "0px",
    "&:focus": {
      outline: "none"
    }
  }
};

const NavigationItem = ({
  classes,
  i,
  offset,
  text,
  currentPage,
  onGoToPage
}) => (
  <div>
    <li
      className={`${classes.dot} dot${currentPage} ${
        currentPage === i ? "active" : ""
      } `}
    >
      <button type="button" className="link" onClick={() => onGoToPage(offset)}>
        <span />
      </button>
    </li>
    <div className={`${classes.tag} tags tag${i}`}>
      <button
        type="button"
        onClick={() => onGoToPage(offset)}
        className={classes.linkTag}
      >
        {text}
      </button>
    </div>
  </div>
);

NavigationItem.propTypes = {
  classes: PropTypes.object,
  currentPage: PropTypes.number,
  i: PropTypes.number,
  onGoToPage: PropTypes.func,
  offset: PropTypes.number,
  text: PropTypes.string
};

export default withStyles(styles)(NavigationItem);
